/* CSS Document */
html, body {
  -ms-touch-action: none;
  /* This is necessary to allow the chart to grab windows touch events */
  margin: 0px;
  /*to deal with iFraming which sets 8px by default in some browsers*/ }
.chartContainer {
  /* DIV that the canvas expands to. #chartContainer is only for backward compatibility with older libraries. */
  display: block;
  font-family: "Roboto", helvetica, sans-serif;
  font-size: 12px;
  position: relative;
  /* chart container must be relative for internal DOM elements to be correct */
  z-index: 1;
  transition: height 0.4s ease-in-out; }
.smartcharts-light .chartContainer {
    background: #ffffff;
    color: #d2d3da; }
.smartcharts-dark .chartContainer {
    background: #2a3052;
    color: #4a4f6c; }
.chartContainer.ciq-draw-mode {
    cursor: none; }
/* Chart Block */
.ciq-chart {
  height: auto;
  width: 100%; }
.smartcharts-light .ciq-chart {
    background: #ffffff; }
.smartcharts-dark .ciq-chart {
    background: #2a3052; }
/* The Chart */
.cq-chart-control-left .stx-panel-control {
  left: 10px; }
.cq-chart-control-left .chartContainer {
  width: calc(100% - 90px);
  margin-left: 90px; }
.sharing .ciq-no-share {
  display: none !important;
  left: -9999px !important; }
