/* --------------------------------------------------------- CHART CONTROLS --------------------------------------------------------- */
.stx_chart_controls {
  /* Chart Control module */
  position: absolute;
  display: block;
  bottom: 60px;
  left: 50%;
  margin-left: -55px;
  height: 27px;
  margin-bottom: 15px;
  width: auto;
  text-align: center;
  z-index: 30; }
.stx_jump_today:hover,
.stx_jump_today:active {
  /* Make buttons full opacity on hover or hit */
  opacity: 1; }
.stx_jump_today {
  /* Button  jumping to today's data */
  color: #fff;
  overflow: hidden;
  position: absolute;
  bottom: 12px;
  right: 10px;
  line-height: 0px;
  height: 25px;
  width: 30px;
  padding: 0px;
  margin-bottom: 17px;
  border-radius: 3px;
  background: #fafafa;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.2);
  transition: background 0.25s;
  cursor: pointer;
  z-index: 30; }
.stx_jump_today span {
  display: inline-block;
  line-height: 25px;
  height: 25px;
  width: 30px;
  padding-top: 25px;
  background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='15.121' height='38.414'%3E%3Cpath fill='none' stroke='%23000' stroke-width='2' stroke-miterlimit='10' d='M.707.707l6 6-6 6'/%3E%3Cpath fill='none' stroke='%23fff' stroke-width='2' stroke-miterlimit='10' d='M.707 25.707l6 6-6 6'/%3E%3Cpath fill='none' stroke='%23000' stroke-width='2' stroke-miterlimit='10' d='M7.707.707l6 6-6 6'/%3E%3Cpath fill='none' stroke='%23fff' stroke-width='2' stroke-miterlimit='10' d='M7.707 25.707l6 6-6 6'/%3E%3C/svg%3E") no-repeat center;
  transition: opacity 0.25s, -webkit-transform 0.2s;
  transition: opacity 0.25s, transform 0.2s;
