.ic-icon, .cq-text-icon {
  display: block;
  height: 24px;
  width: 24px;
  line-height: 32px;
  transition: all 0.2s 0.15s;
  border-radius: 2px;
  box-sizing: border-box;
  text-align: center; }
.ic-icon.brand-blue svg path, .cq-text-icon.brand-blue svg path {
    fill: #2a3052; }
.smartcharts-light .ic-icon.ciq-active-favorite svg > g > g > path, .smartcharts-light .cq-text-icon.ciq-active-favorite svg > g > g > path {
    fill: #e98024 !important;
    stroke: #e98024 !important; }
.smartcharts-dark .ic-icon.ciq-active-favorite svg > g > g > path, .smartcharts-dark .cq-text-icon.ciq-active-favorite svg > g > g > path {
    fill: #e98024 !important;
    stroke: #e98024 !important; }
.ic-icon.ic-clear, .cq-text-icon.ic-clear {
    background: #4b5794; }
.ic-icon.ic-clear.fill svg path, .cq-text-icon.ic-clear.fill svg path {
      fill: #ffffff; }
.ic-icon.ic-clear.stroke svg path, .cq-text-icon.ic-clear.stroke svg path {
      stroke: #ffffff; }
.ic-icon.margin, .cq-text-icon.margin {
    margin: 0 5px; }
.ic-icon svg, .cq-text-icon svg {
    position: relative; }
.ic-icon br, .ic-icon .ic-subtitle, .cq-text-icon br, .cq-text-icon .ic-subtitle {
    display: none; }
.ic-icon-with-sub {
  height: auto !important;
  width: auto !important;
  line-height: 15px; }
.ic-icon-with-sub br, .ic-icon-with-sub .ic-subtitle {
    display: inline; }
.ciq-tooltip {
  position: relative;
  font-size: 12px; }
.ciq-tooltip:after {
    position: absolute;
    opacity: 0;
    pointer-events: none;
    height: 16px;
    width: 65px;
    line-height: 16px;
    bottom: 32px;
    content: attr(tooltip-title);
    left: -100%;
    padding: 8px;
    z-index: 98;
    transition: opacity 0.3s;
    border-radius: 4px; }
.smartcharts-light .ciq-tooltip:after {
      color: #ffffff;
      background: rgba(51, 51, 51, 0.88);
      box-shadow: rgba(51, 51, 51, 0.32); }
.smartcharts-dark .ciq-tooltip:after {
      color: #2a3052;
      background: rgba(255, 255, 255, 0.88);
      box-shadow: rgba(255, 255, 255, 0.32); }
.ciq-tooltip:before {
    position: absolute;
    opacity: 0;
    pointer-events: none;
    border: solid;
    border-width: 4px 4px 0px;
    bottom: 28px;
    content: '';
    left: 40%;
    z-index: 99;
    transition: opacity 0.3s; }
.smartcharts-light .ciq-tooltip:before {
      border-color: rgba(51, 51, 51, 0.88) transparent; }
.smartcharts-dark .ciq-tooltip:before {
      border-color: rgba(255, 255, 255, 0.88) transparent; }
.ciq-tooltip:hover:after, .ciq-tooltip:hover:before {
    opacity: 1; }
.ic-frx, .ic-otc, .ic-wld {
  width: 32px;
  height: 32px;
  position: relative;
  display: inline-block;
  vertical-align: top; }
.ic-frx > span, .ic-otc > span, .ic-wld > span {
    position: absolute; }
.ic-frx > span:last-child {
  top: 8px;
  left: 8px; }
.ic-otc > span {
  top: 5px; }
.ic-otc > span:last-child {
  top: -8px;
  left: 9px; }
.ic-wld > span:last-child {
  top: -9px;
  left: 11px; }
.ic-wld > span:last-child > svg {
    width: 12px;
    height: 8px; }
.ic-frxXPDUSD > svg > g > g > g > path:first-child {
  fill: #e9eaf0; }
.ic-frxXPTUSD > svg > g > g > g > path:first-child {
  fill: #f4f3f3; }
.ic-frxXAGUSD > svg > g > g > g > path:first-child {
  fill: #e1e1e1; }
.no-stroke {
  stroke: none !important; }
.no-fill {
  fill: none !important; }
.smartcharts-mobile .cq-chart-controls .ciq-menu {
  padding: 0px; }
.smartcharts-mobile br, .smartcharts-mobile .ic-subtitle {
  display: none; }
